.form {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.form input[type="text"] {
    height: 40px;
    padding: 0 20px;
    font-size: 20px;
    border: 1px solid #ccc;
    width: 100%;
}

.form button[type="submit"] {
    border: none;
    height: 40px;
    width: 40px;
    background-color: #f04c64;
    color: white;

    display: flex;
    align-items: center;
    justify-content: center;
   
}

.form button[type="submit"]:hover{
    background-color: #b82239;
    transition: 300ms;
}
